<template>
    <div class="pa-wrapper">
        <div class="profile-block">
            <div class="profile-block__wrapper">
                <div class="profile-avatar">
                    <div class="profile-avatar__photo">
                        <img :src="Avatar" alt="photo">
                        <div class="profile-avatar-hover">
                            <img :src="CameraIcon" alt="camera">
                        </div>
                    </div>
                    <button class="profile-avatar__change-photo">Змінити фото</button>
                </div>
                <div class="profile-box">
                    <InputBlock
                        v-for="item in inputs"
                        :key="item.id"
                        :id="item.id"
                        :label="item.label"
                        :mask="item.mask"
                        :showError="item.touched && !item.valid && item.value.length !== 0"
                        :errorMessage="item.errorMessage"
                        className="profile-box__input"
                        @handle-input="checkValue($event, item.id)"
                        @on-click="handleTouched(item.id)"
                    >
                        <div
                            class="profile-box__verification-icon"
                            v-if="item.name === 'email' || item.name === 'phone'"
                        >
                            <img :src="item.verificated ? VerificatedIcon : NotVerificatedIcon" alt="verificate icon">
                        </div>
                        <Tooltip
                            wrapperClass="profile-box__tooltip-wrapper"
                            tooltipClass="tooltip_profile"
                            v-if="item.name === 'email'"
                        >
                            Ви не підтвердили ваш email.<br /> Перейдіть за посиланням в листі, що ми надіслали на вашу email адресу.
                            <button>Надіслати листа</button> ще раз.
                        </Tooltip>
                        <Tooltip
                            wrapperClass="profile-box__tooltip-wrapper"
                            tooltipClass="tooltip_profile"
                            v-if="item.name === 'phone'"
                        >
                            Ви не підтвердили ваш телефон.<br />
                            <button>Надіслати sms</button> ще раз.
                        </Tooltip>
                    </InputBlock>
                </div>
                <div class="profile-reactions">
                    <ButtonWithCount
                        v-for="item in reactions"
                        :key="item.id"
                        :text="item.text"
                        :amount="item.amount"
                        className="profile-reactions__btn"
                    />
                </div>
                <Button
                    text="Зберегти"
                    :disabled="!isAllValid"
                    className="profile-save-btn"
                />
            </div>
        </div>
    </div>
</template>

<script>
import is from 'is_js'

import InputBlock from '@/components/Input'
import Tooltip from '@/components/Tooltip'
import ButtonWithCount from '@/components/Buttons/ButtonWithCount'
import Button from '@/components/Buttons/Button'

import Avatar from '@/assets/img/actor.png'
import VerificatedIcon from '@/assets/img/icon-done.svg'
import NotVerificatedIcon from '@/assets/img/icon-alert.svg'
import CameraIcon from '@/assets/img/icon-camera.svg'

export default {
    components: {
        InputBlock, Tooltip, ButtonWithCount, Button
    },
    data () {
        return {
            Avatar,
            VerificatedIcon,
            NotVerificatedIcon,
            CameraIcon,
            inputs: [
                {
                    id: 1, 
                    type: 'text',
                    value: '',
                    label: 'Ім\'я та Прізвище',
                    errorMessage: 'Мінімум 2 та максимум 50 символів',
                    name: 'name',
                    touched: false,
                    valid: true
                },
                {
                    id: 2, 
                    type: 'email',
                    value: '',
                    label: 'E-mail',
                    errorMessage: 'Введіть коректний email',
                    name: 'email',
                    touched: false,
                    valid: true,
                    verificated: false
                },
                {
                    id: 3, 
                    type: 'text',
                    value: '',
                    label: 'Номер телефона',
                    errorMessage: 'Введіть коректний номер телефону',
                    name: 'phone',
                    touched: false,
                    valid: true,
                    verificated: true
                },
                {
                    id: 4, 
                    type: 'text',
                    value: '',
                    label: 'Місто',
                    errorMessage: 'Мінімум 2 та максимум 50 символів',
                    name: 'city',
                    touched: false,
                    valid: true
                }
            ],
            reactions: [
                {
                    id: 1,
                    text: 'Мої відгуки',
                    amount: 11
                },
                {
                    id: 2,
                    text: 'Мої вподобайки',
                    amount: 13
                },
            ]
        }
    },
    methods: {
        checkValue: function(value, id) {
            this.inputs.forEach(item => {
                if (item.id === id) {
                    const length = value.length === 0

                    item.value = value

                    if (item.name === 'name' || item.name === 'city') {
                        if (value.length >= 2 && value.length <= 50 || length) {
                            item.valid = true
                        } else {
                            item.valid = false
                        }
                    }

                    if (item.name === 'email') {
                        if (is.email(value) || length) {
                            item.valid = true
                        } else {
                            item.valid = false
                        }
                    }
                    
                    if (item.name === 'phone') {
                        if (value.length === 13 || length) {
                            item.valid = true
                        } else {
                            item.valid = false
                        }
                    }
                }

            })
        },
        handleTouched: function(id) {
            this.inputs.forEach(item => item.id === id ? item.touched = true : null)
        }
    },
    computed: {
        isAllValid: function() {
            let allValid = false
            const touched = !!this.inputs.find(item => item.touched)
            let allEmpty = !!!this.inputs.find(item => item.value)

            if (touched && !allEmpty) {
                allValid = true
                this.inputs.forEach(item => {
                    if (item.touched) {
                        if (item.valid && allValid) {
                            allValid = true
                        } else {
                            allValid = false
                        }
                    }
                })
            }

            return allValid
        }
    }
}
</script>

