    <button
        class="button"
        :class="[{'button_active': activeClass}, className]"
        @click="onClick"
    >
        <div class="button__text">
            {{ text }}
        </div>
        <div class="button__amount">
            {{ amount }}
        </div>
    </button>
</template>

<script>
export default {
    props: {
        text: {
            type: String,
            required: true
        },
        amount: {
            type: Number,
            required: true
        },
        activeClass: Boolean,
        className: {
            type: String,
            default: ''
        }
    },
    methods: {
        onClick: function() {
            this.$emit('on-click')
        }
    }
}
</script>

